let Ajax = require("../util/Ajax.js")
export default {
	data() {
		return {
			isLoading:false,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			alibumList:[
			],
			page:1
		}
	},
	onLoad() {
		this.getAlbum(this.page);
	},
	onShow(){
		if(uni.getStorageSync("isF5Index")==true){
			this.alibumList=[];
			this.page=1;
			this.getAlbum(this.page);
			uni.setStorageSync("isF5Index",false);
		}
	},
	onPullDownRefresh(){
		this.alibumList=[];
		this.page=1;
		this.getAlbum(this.page);
	},
	methods: {
		showPic(e){
			var id=e.currentTarget.dataset.id;
			console.log(id);
			uni.navigateTo({
				url:"../pic/pic?id="+id
			})
		},
		//加载首页数据
		getAlbum(e){
			var that = this;
			Ajax.get({url:'album/getalbum',data:{
				page:e
			}})
			  .then((res)=>{//请求成功的回调函数
				that.alibumList=that.alibumList.concat(res.data);
				if(res.data!=""){that.page+=1};
				uni.hideLoading();
			  }).catch((res)=>{//捕获请求失败时的回调函数
			    console.log(res)
			  })
		}
	},
	onReachBottom(e){
		uni.showLoading({
			mask:true,
			title:"加载更多"
		})
		this.getAlbum(this.page);
		
	},
	
}
